/* src/App.css */
body, html {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
    color: white;
    background-color: #000;
    height: 100%;
}

.App {
    text-align: center;
    padding: 20px;
    display: flex;
    flex-direction: column;
    height: 100vh; /* Utiliser toute la hauteur de l'écran */
}

.App-header {
    margin-bottom: 20px;
}

h2 {
    font-size: 2rem;
    margin-top: 20px;
}

main {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-around; /* Espacement uniforme entre les sections */
}

section {
    margin: 10px 0; /* Espacement vertical entre les sections */
    padding: 10px; /* Espacement interne */
    flex: 1; /* Permet aux sections de prendre une part égale de l'espace disponible */
}

h3, h4 {
    font-size: 1.5rem;
    margin-bottom: 10px;
}

p {
    font-size: 1rem;
    line-height: 1.5;
}

ul {
    list-style-type: none;
    padding: 0;
}

li {
    margin-bottom: 10px;
}

.skills-section {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.skills {
    width: 100%;
    max-width: 300px;
    height: 200px; /* Ajustez la hauteur pour s'adapter à l'écran */
    display: flex;
    justify-content: center;
    align-items: center;
}

.social-links {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-top: 20px;
}

.social-links a {
    display: flex;
    align-items: center;
    color: white;
    text-decoration: none;
    font-size: 1rem;
}

.social-links a svg {
    margin-right: 5px;
}

/* Styles pour TechSphere */
.tech-sphere {
    width: 100%;
    height: 100%;
    perspective: 1000px;
}

.tech-sphere .icon {
    position: absolute;
    font-size: 2rem;
    color: #61dafb;
    transition: transform 0.3s ease-out;
}

@keyframes glow {
    0% { filter: drop-shadow(0 0 2px rgba(97, 218, 251, 0.7)); }
    50% { filter: drop-shadow(0 0 5px rgba(97, 218, 251, 0.9)); }
    100% { filter: drop-shadow(0 0 2px rgba(97, 218, 251, 0.7)); }
}

.tech-sphere .icon {
    animation: glow 2s infinite;
}

/* Media Queries pour la responsivité */
@media (max-width: 768px) {
    h2 {
        font-size: 1.5rem;
    }

    h3 {
        font-size: 1.2rem;
    }

    p {
        font-size: 0.9rem;
    }

    .skills {
        height: 150px; /* Ajustez la hauteur pour les écrans plus petits */
    }
}

@media (max-width: 480px) {
    h2 {
        font-size: 1.2rem;
    }

    h3 {
        font-size: 1rem;
    }

    p {
        font-size: 0.8rem;
    }

    .skills {
        height: 100px; /* Ajustez la hauteur pour les petits écrans */
    }

    .social-links {
        flex-direction: column;
        gap: 10px;
    }
}
